import '../styles/component.scss';

import Sortable from 'sortablejs';

const DEFAULT = {
	containerId: '',
	controlName: '',
	optionId: '',
	ajaxReload: true,
};

export class RedsignTuningOptionDnD
{
	container = null;
	elements = [];

	constructor(options = {})
	{
		this.options = {...DEFAULT, ...options};

		document.addEventListener("DOMContentLoaded", this.init.bind(this));
	}

	init()
	{
		this.container = document.getElementById(this.options.containerId);

		if (!this.container)
			return;

		var elements = [...this.container.querySelectorAll('.js-rstuning-sortable')];

		for (var i = 0; i < elements.length; i++)
		{
			Sortable.create(elements[i], {
				handle: '.js-rstuning-sortable-handle',
				ghostClass: "rstuning__option__dd__ghost",
				forceFallback: true,
				fallbackClass: 'rstuning__option__dd__fallback',
				onEnd: (event) => {
					var arSortedResult = [];
						// rsTuningComponent = new RS.TuningComponent(),
						// tuningObj = rsTuningComponent.getTuningComponent();


					// if (this.container && this.options.ajaxReload)
					// {
					// 	tuningObj.setAttribute('data-reload', 'Y')
					// }

					var elements = this.container.querySelectorAll('input[name="' + this.options.controlName + '[]"]');

					if (elements && elements.length > 0)
					{
						for (i = 0; i < elements.length; i++)
						{
							arSortedResult.push(elements[i].value)
						}
					}

					var event = new CustomEvent('rs.tuning.option.dd.onEnd', {'detail' : {'element': this.container, 'value': arSortedResult}});
					document.dispatchEvent(event);

					rsTuningComponent.formSubmit();
				},
			})
		}
	}
}
